import { useState, useEffect } from "react";
import { useAuth } from "@/hooks/useAuth";
import { useLocation } from "wouter";
import { Button } from "@/components/ui/button";
import { AuthModal } from "@/components/AuthModal";
import { Film } from "lucide-react";

export default function Landing() {
  const { isAuthenticated } = useAuth() as any;
  const [, setLocation] = useLocation();
  const [isAuthModalOpen, setIsAuthModalOpen] = useState(false);

  useEffect(() => {
    if (isAuthenticated) {
      setLocation("/films");
    }
  }, [isAuthenticated, setLocation]);

  return (
    <div className="min-h-screen text-white">
      {/* Hero Section */}
      <section className="relative min-h-screen flex items-center">
        {/* Decorative background */}
        <div className="absolute inset-0 bg-cinema-gradient pointer-events-none" />

        <div className="relative z-10 container mx-auto px-4 py-16">
          <div className="max-w-3xl">
            <div className="flex items-center gap-3 mb-4">
              <Film className="w-8 h-8 text-cinema-red" />
              <span className="uppercase tracking-widest text-cinema-red">B2B </span>
            </div>
            <h1 className="text-4xl md:text-6xl font-extrabold leading-tight">
                 {" "}
              <span className="text-cinema-red"> </span> -
            </h1>
            <p className="text-gray-300 mt-6 text-lg">
                      .
                  .
            </p>

            <div className="mt-10">
              <Button
                className="bg-cinema-red hover:bg-red-700 text-white px-8 py-3 rounded-lg font-semibold"
                data-testid="get-started-button"
                onClick={() => setIsAuthModalOpen(true)}
              >
                 / 
              </Button>
            </div>
          </div>
        </div>
      </section>

      <AuthModal isOpen={isAuthModalOpen} onClose={() => setIsAuthModalOpen(false)} />
    </div>
  );
}
